@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url('../fonts/Roboto-400.woff2') format('woff2'),
    url('../fonts/Roboto-400.woff') format('woff');
}

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src:
    url('../fonts/Roboto-Italic-400.woff2') format('woff2'),
    url('../fonts/Roboto-Italic-400.woff') format('woff');
}

